<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" ref="form" label-width="120px" class="form" :rules="rules">
        <el-form-item prop="campusId" label="校区：">
          <el-checkbox-group style="width: 50%" v-model="form.campusId">
            <el-checkbox v-for="(item, idx) in this.searchData[0].option"
                         :key="idx" border
                         :label="item.value">{{item.label}}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>


        <el-form-item label="标题：" prop="title">
          <el-input v-model="form.title" style="width: 40%">
          </el-input>
        </el-form-item>
        <el-form-item label="正文内容：" prop="content">
          <wang-editor :content.sync="form.content" v-if="form.content || !id"></wang-editor>
        </el-form-item>
      </el-form>
      <div class="btns">
        <div @click="doSubmit()" class="btn-submit">确定</div>
        <div @click="$router.go(-1)" class="btn-clear" style="margin-left: 30px">返回</div>
      </div>
    </main-part>
  </div>
</template>

<script>
  export default {
    data () {
      // 验证正文内容
      const checkContent = (rule, value, callback) => {
        if (!value || value === '<p><br></p>') {
          callback(new Error('请输入正文内容'))
        } else {
          callback()
        }
      }
      let vm = this
      return {
        id: vm.$route.query.id,
        announcementDetail: [],
        nav: [{name: '教师招聘', path: 'go(-1)'}, {name: '发布招聘公告', path: ''}],
        searchData: [{type: 'select', title: '校区', model: 'campusId', option: []}],
        text: '发布成功',
        form: {
          campusId: [],
          title: null,
          content: null
        },
        rules: {
          campusId: {required: true, message: '请选择校区', trigger: 'change'},
          title: {required: true, message: '请输入标题', trigger: 'blur'},
          content: {required: true, validator: checkContent, trigger: 'change'},
        },
      }
    },
    methods: {
      // 获取公告详情
      async getDetail () {
        this.announcementDetail = await this.$ajax({
          url: '/notice/getTeacherRecruitNoticeDetail.do',
          el: '.form',
          query: {
            id: this.id
          }
        })
        this.form.title = this.announcementDetail.title
        this.form.campusId = this.announcementDetail.campusId
        this.form.content = this.announcementDetail.content
      },
      // 获取所有校区
      async getCampus () {
        let arr = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
        this.searchData[0].option = []
        arr.forEach(e => {
          this.searchData[0].option.push({
            value: e.id,
            label: e.name
          })
        })
      },
      async doSubmit () {
        await this.$refs.form.validate()
        let url = '/notice/addTeacherRecruitNotice.do'
        if (this.id) {
          url = '/notice/changeTeacherRecruitNoticeDetail.do'
          this.text = '修改成功'
          this.form.id = this.id
        }
        await this.$ajax({
          url,
          el: '.form',
          query: this.form,
        })
        this.$remind(this.text)
        this.$router.push('/personnel/recruitment')
      },
    },
    watch: {
      'form.content': function () {
        setTimeout(() => {
          this.$refs.form.validate()
        }, 1)
      }
    },
    mounted () {
      this.getCampus()
      if (this.id) {
        this.getDetail()
      }
    }
  }
</script>

<style lang='sass' scoped>
    .form
      width: 90%
      margin-top: 30px
    .btns
      padding-left: 120px
</style>
